import axiosInstance from '../../axios.config'
import {
  GetVLXPriceRequest,
  GetVLXPriceSuccess,
  GetVLXPriceError,
  GetBTCPriceRequest,
  GetBTCPriceSuccess,
  GetBTCPriceError,
  GetTOKENPriceRequest,
  GetTOKENPriceSuccess,
  GetTOKENPriceError,
} from './action.types'
import { Dispatch } from 'redux'
import { priceApiEndPoints, VLXPriceData } from '../../common/consts'

export const getTokenPrice =
  () => async (dispatch: Dispatch<GetTOKENPriceRequest | GetTOKENPriceSuccess | GetTOKENPriceError>) => {
    try {
      dispatch({
        type: 'GET_TOKEN_PRICE_REQUEST',
        payload: {
          TOKENPrice: 2,
        },
      } as GetTOKENPriceRequest)

      dispatch({
        type: 'GET_TOKEN_PRICE_SUCCESS',
        payload: {
          TOKENPrice: 2,
        },
      } as GetTOKENPriceSuccess)
    } catch (error: any) {
      dispatch({
        type: 'GET_TOKEN_PRICE_ERROR',
        payload: {
          TOKENPrice: 2,
          error: error.message,
        },
      } as GetTOKENPriceError)
    }
  }

export const getVLXPrice =
  () => async (dispatch: Dispatch<GetVLXPriceRequest | GetVLXPriceSuccess | GetVLXPriceError>) => {
    try {
      dispatch({
        type: 'GET_VLX_PRICE_REQUEST',
        payload: {},
      } as GetVLXPriceRequest)

      const response = await axiosInstance.post<any>(priceApiEndPoints.VLX.api, priceApiEndPoints.VLX.req)

      const { Success } = response.data
      if (Success === true) {
        const { Data } = response.data
        dispatch({
          type: 'GET_VLX_PRICE_SUCCESS',
          payload: {
            VLXPrice: Data.Price || VLXPriceData.PRICE,
            VLXPriceChange: Data.Percent || VLXPriceData.PERCENT,
            VLXPriceDiff: Data.PriceDiff || VLXPriceData.PRICE_DIFF,
          },
        } as GetVLXPriceSuccess)
      } else {
        const { Error } = response.data
        dispatch({
          type: 'GET_VLX_PRICE_ERROR',
          payload: {
            error: Error.Msg,
            VLXPrice: VLXPriceData.PRICE,
            VLXPriceChange: VLXPriceData.PERCENT,
            VLXPriceDiff: VLXPriceData.PRICE_DIFF,
          },
        } as GetVLXPriceError)
      }

      //temporary dispatch without api, will be replaced after api integrated

      // dispatch({
      //   type: 'GET_VLX_PRICE_SUCCESS',
      //   payload: {
      //     VLXPrice: 1.187,
      //     VLXPriceChange: 9.45,
      //   },
      // } as GetVLXPriceSuccess)
    } catch (error: any) {
      dispatch({
        type: 'GET_VLX_PRICE_ERROR',
        payload: {
          error: error.message,
          VLXPrice: VLXPriceData.PRICE,
          VLXPriceChange: VLXPriceData.PERCENT,
          VLXPriceDiff: VLXPriceData.PRICE_DIFF,
        },
      } as GetVLXPriceError)
    }
  }

export const getBTCPrice =
  () => async (dispatch: Dispatch<GetBTCPriceRequest | GetBTCPriceSuccess | GetBTCPriceError>) => {
    try {
      dispatch({
        type: 'GET_BTC_PRICE_REQUEST',
        payload: {},
      } as GetBTCPriceRequest)

      // fetch btc data
      axiosInstance
        .get(priceApiEndPoints.BTC.api)
        .then(function (response: any) {
          // handle success
          let btcToUsd = response.data.data.amount
          dispatch({
            type: 'GET_BTC_PRICE_SUCCESS',
            payload: {
              BTCPrice: btcToUsd,
            },
          } as GetBTCPriceSuccess)
        })
        .catch(function (error: any) {
          // handle error
          dispatch({
            type: 'GET_BTC_PRICE_ERROR',
            payload: {
              error: error.message,
            },
          } as GetBTCPriceError)
        })
    } catch (error: any) {
      dispatch({
        type: 'GET_BTC_PRICE_ERROR',
        payload: {
          error: error.message,
        },
      } as GetBTCPriceError)
    }
  }
